.justify-between {
    justify-content: space-between;
}

.items-center {
    align-items: center;
}

.flex {
    display: flex;
}

.title-btn {
    text-align: right;
    font-size: 20px;
}

.card {
    background: rgba(243, 244, 246, 0.7);
    border-radius: 7px;
    padding: 40px 16px;
    height: 150px;
}

#run_guide .tag {
    border-radius: 14px;
    border: 1px solid silver;
    min-width: 58px;
    padding: 6px 10px;
    margin-left: 7px;
}

#run_guide .tag:hover {
    border: 1px solid black;
}


/*Guide Section*/
.guide-card {
    background-size: cover;
    border: none;
    border-radius: 7px;
    height: 150px;
    color: white;
}

.guide-card-body {
    -webkit-box-flex: 1;
    -ms-flex: 1 1 auto;
    flex: 1 1 auto;
    padding: 1.25rem;
}

.color-overlay {
    -webkit-transition: all 100ms ease-in;
    -o-transition: all 100ms ease-in;
    transition: all 100ms ease-in;
}

.pc-margin-20 {
    margin-bottom: 20px;
}

@media screen and (max-width: 991px) {
    .margin-20 {
        margin-bottom: 20px;
    }
}