.rounded {
    border-radius: 50%;
    width: 23px;
}

.view-ul {
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 40px;
}

.view-li {
    list-style: disc;
}

.issue-identified {
    color: blue;
}
.issue-identified:hover {
    color: rgb(66, 0, 128);
}

#news_notice_image {
    background-image: url(../../assets/images/news/notice.eng.png);
    background-size: cover;
}
#news_update_image {
    background-image: url(../../assets/images/news/update.eng.png);
    background-size: cover;
}